/*Fonts for typography
@mixin setFont(
	$fontType,
	$fontSize,
	$fontWeight: "regular",
	$color: "default"
) {
	$fontKeyMap: map-get(
		$map: $fonts,
		$key: $fontType,
	);

	font-family: unquote(map-get($map: $fontKeyMap, $key: "type"));
	font-size: map-get($map: $fontKeyMap, $key: $fontSize);
	font-weight: map-get($map: $fontKeyMap, $key: $fontWeight);
	@if $color != "default" {
		color: $color;
	}

	@content;
}
*/
:root {
  --fs-400: .85rem;
  --fs-500: .9625rem;
  --fs-600: 1rem;
  --fs-900: 2rem;
}

@media (min-width: 60em) {
  :root {
    --fs-900: 3rem;
  }
}
:root {
  --fw-regular: 400;
  --fw-medium: 500;
  --fw-semiBold: 700;
}

