/****** Elad Shechter's RESET *******/
/*** box sizing border-box for all elements ***/
*,
*::before,
*::after {
  box-sizing: border-box;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

button {
  background-color: transparent;
  color: inherit;
  border-width: 0;
  padding: 0;
  cursor: pointer;
}

figure {
  margin: 0;
}

input::-moz-focus-inner {
  border: 0;
  padding: 0;
  margin: 0;
}

ul,
ol,
dd {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
}

p {
  margin: 0;
}

cite {
  font-style: normal;
}

fieldset {
  border-width: 0;
  padding: 0;
  margin: 0;
}

body {
  font-family: var(--ff-primary);
  font-size: var(--fs-400);
  display: flex;
  justify-content: center;
  padding: 5em 0;
  background-color: var(--clr-neutral-100);
  min-height: 100vh;
  background: url(../../../images/bg-pattern-top-desktop.svg), url(../../../images/bg-pattern-bottom-desktop.svg);
  background-size: max(38%, 400px), 73%;
  background-position: top left, bottom right;
  background-repeat: no-repeat;
}

.main {
  display: flex;
  flex-direction: column;
}
.main__container--1 {
  color: var(--clr-primary-700);
  text-align: center;
  font-weight: var(--fw-semiBold);
}
.main__heading__header {
  font-size: var(--fs-900);
  margin-bottom: 0.2em;
  line-height: 1;
}
.main__heading__intro {
  font-size: var(--fs-400);
  font-weight: var(--fw-regular);
  line-height: 1.5;
  color: var(--clr-neutral-700);
}

.testomnial {
  color: var(--clr-neutral-400);
}
.testomnial__profile__username {
  font-weight: var(--fw-semiBold);
  font-size: var(--fs-400);
  margin-bottom: 0.2em;
}
.testomnial__profile__verified {
  color: var(--clr-primary-400);
}

.main__ratings {
  display: flex;
  flex-direction: column;
}
.main__heading {
  margin-bottom: 1em;
}
.main__container {
  display: flex;
  flex-direction: column;
}

.ratings {
  display: flex;
  flex-direction: column;
}
.ratings__item {
  display: flex;
  flex-direction: column;
  padding: 1.2em;
  align-items: center;
  margin-bottom: 1em;
  background-color: var(--clr-neutral-400);
  border-radius: 10px;
}
.ratings__item__stars {
  display: flex;
  gap: 10px;
  margin-bottom: 1em;
}

.wrapper {
  width: 90vw;
  max-width: 1400px;
}

.testomnial {
  background-color: var(--clr-primary-700);
  padding: 2.5em;
  border-radius: 10px;
  margin-bottom: 1.5em;
}
.testomnial__profile {
  display: flex;
  align-items: center;
}
.testomnial__profile__img {
  max-width: 40px;
  border-radius: 50%;
  margin-right: 1.5em;
}
.testomnial__customer-quote {
  margin: 0;
  line-height: 1.5;
}

@media (min-width: 60em) {
  .wrapper {
    width: 82vw;
  }

  .main {
    height: 100%;
  }
  .main__container {
    flex-direction: row;
  }
  .main__container--1 {
    flex: 0 0 53%;
    text-align: unset;
    margin-bottom: 2em;
  }
  .main__container--2 {
    flex: 0 0 47%;
    justify-content: space-between;
    gap: 10px;
  }
  .main__heading {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .main__heading__header {
    max-width: 13ch;
  }
  .main__heading__intro {
    max-width: 38ch;
  }
  .main__ratings {
    flex: 1;
    justify-content: center;
  }

  .ratings__item {
    flex-direction: row;
    width: 85%;
    padding: 1.5em;
  }
  .ratings__item:nth-of-type(1) {
    align-self: flex-start;
  }
  .ratings__item:nth-of-type(2) {
    align-self: center;
  }
  .ratings__item:nth-of-type(3) {
    align-self: flex-end;
  }
  .ratings__item__stars {
    margin: 0 3em 0 1.5em;
  }
  .ratings__item__stars__img {
    min-width: 20px;
  }

  .testomnial {
    padding: 3.5em 2em;
    flex: 0 0 32%;
    margin-bottom: 0;
  }
  .testomnial:first-of-type {
    align-self: flex-start;
  }
  .testomnial:nth-of-type(2) {
    align-self: center;
  }
  .testomnial:nth-of-type(3) {
    align-self: flex-end;
  }
  .testomnial__profile {
    margin-bottom: 1.5em;
  }
  .testomnial__customer-quote {
    font-size: var(--fs-400);
    line-height: 1.5;
    max-width: 27ch;
  }
}